export const getTodoList = state => { // 规整源数据
    return state.todoList.map(todo => {
        return {
            id: todo.id,
            title: todo.title,
            count: todo.record.filter((data) => !data.checked).length, 
            locked: todo.locked,
            isDelete: todo.isDelete,
            record: todo.record,
          };}).filter(todo => !todo.isDelete);  // 过滤删除的
}

export const getTodoId = (state, getters) => (id) => { // state 是必选参数，也可以使用其他getters属性
    return getters.getTodoList.find (todo => {
        if (todo.id === id) {
            return todo;
        }
    });
}